<template>
  <div class="iscroll">
    <div class="iscroll-content"><slot></slot></div>
  </div>
</template>

<script>
  import IScroll from 'iscroll'

  export default {
    name: 'MScroller',
    props: {
      change: {
        type: Boolean
      },
      config: {
        type: Object,
        default: _ => ({
          scrollbars: true,
          mouseWheel: true
        })
      }
    },
    watch: {
      change (n) {
        if (n) this.$IScroll.refresh()
      }
    },
    mounted () {
      this.$IScroll = new IScroll(this.$el, this.config)
    },
    destroyed () {
      this.$IScroll.destroy()
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



