<template>
  <div class="select" v-click-outside="closeHandle" v-resize="handleResize" :class="[stateClass]">
    <slot name="label">
      <div class="select-label">
        <div class="select-label_text left">{{label}}</div>
        <div class="select-label_angle right"><span class="caret"></span></div>
      </div>
    </slot>
    <m-opation-group :class="[listClass]" ref="popper" v-model="popperShow" @itemSelect="itemSelect">
      <slot>
        <m-list-item v-for="(item, key) in datas" :key="key" :value="tempValue" :data-post="item">{{item.label}}</m-list-item>
      </slot>
    </m-opation-group>
  </div>
</template>

<script>
  import OpationGroup from './OpationGroup.vue'
  import MPP from '../../../mixins/MPopperParent.js'

  export default {
    name: 'MSelect',
    mixins: [MPP],
    props: {
      value: Object,
      labelKey: {
        type: String,
        default: 'label'
      },
      datas: {
        type: Array,
        default: () => ([])
      },
      labelDft: {
        type: String,
        default: ''
      }
    },
    data: () => ({
      selfvalue: {}
    }),
    computed: {
      label () {
        let temp = this.labelDft
        let tempv = this.value || this.selfvalue
        if (tempv[this.labelKey]) temp = tempv[this.labelKey]
        return temp
      },
      tempValue () {
        return this.value || this.selfvalue
      }
    },
    watch: {
      datas (n, o) {
        if (n.length > 0) this.init()
      }
    },
    methods: {
      init () {
        if (this.datas.length > 0) {
          let i = -1
          while (++i < this.datas.length) {
            let d = this.datas[i]
            if (d.hasOwnProperty('active') && d.active) {
//              delete d.active
              this.$emit('input', d)
              this.selfvalue = d
              break
            }
          }
        }
      },
      initPopperHeight (v) {
        let temph = v * this.datas.length + 2
        if (this.maxHeight && temph > this.maxHeight) temph = this.maxHeight
        this.popperElm.style.height = temph + 'px'
      }
    },
    mounted () {
      this.init()
    },
    components: {'MOpationGroup': OpationGroup}
  }
</script>

<style lang="scss" src="./style.scss"></style>



