<template>
  <button class="btn"
          :class="[waves,typeClass]"
          :type="type"
          :disabled="disabled"
          :style="style"
          v-if="!href">
    <slot></slot>
  </button>
  <router-link class="btn"
               :class="[waves, typeClass]"
               :href="linkHref"
               :disabled="disabled"
               :target="target"
               :to="href"
               :style="style"
               v-else><slot></slot></router-link>
</template>

<script>
  const TYPE_MAP = {
    'info': 'info_bg white_txt',
    'success': 'success_bg white_txt',
    'warning': 'warning_bg white_txt',
    'error': 'error_bg white_txt'
  }
  export default {
    name: 'MBtn',
    props: {
      href: [String, Object],
      target: String,
      waves: {
        type: String,
        default: 'waves-effect'
      },
      type: {
        type: String,
        default: ''
      },
      disabled: Boolean,
      width: Number,
      height: {
        type: Number,
        default: 32
      }
    },
    computed: {
      typeClass () {
        return TYPE_MAP[this.type]
      },
      linkHref () {
        return this.disabled ? null : this.href
      },
      style () {
        let temp = {}
        temp.height = this.height + 'px'
        temp.lineHeight = this.height + 'px'
        if (this.width) {
          temp.width = this.width + 'px'
        }
        return temp
      }
    },
    mounted () {
      if (this.$el.className.indexOf('_bg') !== -1) this.$el.classList.add('hover')
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



