<template>
  <transition name="fade">
    <m-scroller class="list select-list z-d1" v-show="visible" :change="visible" :config="scrollConfig" @itemSelect="itemSelect" ref="iscroller">
      <slot></slot>
    </m-scroller>
  </transition>
</template>

<script>
  import MPopper from '../../../mixins/MPopper.js'

  export default {
    name: 'MOpationGroup',
    props: {
      scrollConfig: {}
    },
    mixins: [MPopper],
    methods: {
      itemSelect (d) {
        this.$emit('itemSelect', d)
      },
      actionPopper (bl) {
        this.visible = bl === undefined ? !this.visible : bl
        this.$emit('statusChange', this.visible)
        if (!this.visible) return false
        const timeout = setTimeout(_ => {
          const tempel = this.$el.querySelector('.list-item')
          if (tempel) this.$parent.initPopperHeight(tempel.clientHeight)
          this.$refs.iscroller.$IScroll.refresh()
          clearTimeout(timeout)
        }, 10)
      }
    }
  }
</script>



