<template>
  <transition name="fade">
    <div class="list dropdown-list z-d1" v-show="visible">
      <slot></slot>
    </div>
  </transition>
</template>

<script>
  import MPopper from '../../../mixins/MPopper.js'

  export default {
    name: 'MListDropdown',
    mixins: [MPopper]
  }
</script>



