<template>
  <div class="list-item" :class="[wavesClass, activeClass]" :disabled="disabled" @click="itemSelect">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: 'MListItem',
    props: {
      disabled: Boolean,
      waves: {
        type: Boolean,
        default: true
      },
      dataPost: Object,
      value: Object
    },
    methods: {
      itemSelect () {
        this.$parent.$emit('itemSelect', this.dataPost)
      }
    },
    computed: {
      wavesClass () {
        return this.waves === true && !this.disabled ? 'waves-effect' : ''
      },
      activeClass () {
        return this.value && this.value.value === this.dataPost.value ? 'active' : ''
      }
    },
    mounted () {
      if (!this.$parent) throw new Error('m-list-item m-select m-list m-dropdown')
    }
  }
</script>



