<template>
  <div class="col" :style="style">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: 'MCol',
    computed: {
      gutter () {
        return this.$parent.gutter
      },
      style () {
        let tempstyle = {}
        if (this.gutter) {
          tempstyle.paddingLeft = this.gutter / 2 + 'px'
          tempstyle.paddingRight = tempstyle.paddingLeft
        }
        return tempstyle
      }
    }
  }
</script>



