<template>
  <transition name="fade">
    <div class="msg" :class="[theme]" v-show="visible">
      <div class="msg-overflow" @click="overflowClickClose"></div>
      <div class="msg-content" :class="shadow">
        <div class="msg-content_head" v-if="title" v-html="title"></div>
        <div class="msg-content_body" v-if="content"><m-content></m-content></div>
        <div class="msg-content_foot" v-if="buttons.length > 0">
          <m-btn v-for="(item, key) in buttons" :key="key" @click.native="buttonClick(key)" :class="[item.theme]">{{item.label}}</m-btn>
        </div>
        <div class="close msg-content_close" @click="closeClick" v-if="hasClose"></div>
      </div>
    </div>
  </transition>
</template>

<script>
  import MPopper from '../../../mixins/MPopper.js'
  import Content from './Content.js'

  export default {
    mixins: [MPopper],
    methods: {
      buttonClick (idx) {
        this.callbackFun(idx)
      },
      closeClick () {
        this.actionPopper(false)
        this.callbackFun('close')
      },
      overflowClickClose () {
        if (this.overflowClose) {
          this.actionPopper(false)
          this.callbackFun('close')
        }
      },
      actionPopper (bl) {
        this.visible = bl === undefined ? !this.visible : bl
        if (!bl) {
          const timeout = setTimeout(_ => {
            document.body.style.overflow = 'auto'
            clearTimeout(timeout)
          }, 200)
        }
      },
      callbackFun (...arg) {
        if (this.callback) {
          arg.unshift(this)
          this.callback.apply(this, arg)
        }
      }
    },
    components: { 'm-content': Content }
  }
</script>

<style lang="scss" src="./style.scss"></style>



