<template>
  <div class="input-group" :class="[maskClass]" :disabled="disabled">
    <input class="input"
           :type="type"
           :value="valuecc"
           :max="max"
           :min="min"
           :step="step"
           :placeholder="placeholder"
           :style="inpStyle"
           @input="inputHandle"
    >
    <div class="input-number">
      <div class="input-number_button waves-effect" @click="numAction(-1)">-</div>
      <div class="input-number_button waves-effect" @click="numAction(1)">+</div>
    </div>
    <div class="input-label" v-if="labelPos" :class="labelClass" :style="labelStyle"><slot></slot></div>
  </div>
</template>

<script>
  import Forms from '../../../mixins/Forms.js'

  export default {
    name: 'MInput',
    mixins: [Forms],
    props: {
      type: {
        type: String,
        default: 'text' // tel number password tel url
      },
      height: {
        type: Number,
        default: 32
      },
      labelWidth: Number,
      labelPos: String,
      max: Number,
      min: Number,
      step: Number
    },
    data: () => ({
      labelPosw: 10
    }),
    methods: {
      inputHandle (e) {
        let tempv = e.target.value
        if (this.value !== false) this.$emit('input', tempv)
        else this.formModel = tempv
      },
      numAction (p) {
        let temp = this.value + (p * this.step)
        if (temp <= this.max && temp >= this.min) this.$emit('input', this.value + (p * this.step))
      }
    },
    computed: {
      valuecc () {
        return this.value || this.formModel
      },
      labelClass () {
        return this.labelPos ? 'pos-' + this.labelPos : ''
      },
      labelStyle () {
        let temp = {}
        temp.height = (this.height - 2) + 'px'
        temp.lineHeight = (this.height - 2) + 'px'
        if (this.labelWidth) {
          temp.width = this.labelWidth + 'px'
          temp.textAlign = 'center'
        }
        return temp
      },
      inpStyle () {
        let temp = {}
        if (this.labelPos && this.labelPos === 'left') temp.paddingLeft = (this.labelPosw + 16) + 'px'
        if (this.labelPos && this.labelPos === 'right') temp.paddingRight = (this.labelPosw + 16) + 'px'
        temp.height = this.height + 'px'
        temp.lineHeight = this.height + 'px'
        return temp
      }
    },
    mounted () {
      const inpiconEl = this.$el.querySelector('.input-label')
      if (inpiconEl) {
        const timeout = setTimeout(_ => {
          this.labelPosw = this.labelWidth || inpiconEl.clientWidth
          clearTimeout(timeout)
        }, 10)
      }
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



