<template>
  <table>
    <slot></slot>
    <slot name="thead">
      <thead v-if="datas.thead">
      <tr v-for="row in datas.thead">
        <th v-for="col in row">{{col}}</th>
      </tr>
      </thead>
    </slot>
    <slot name="tbody">
      <tbody v-if="datas.tbody">
      <tr v-for="row in datas.tbody">
        <td v-for="col in row">{{col}}</td>
      </tr>
      </tbody>
    </slot>
    <slot name="tfoot">
      <tfoot v-if="datas.tfoot">
      <tr v-for="row in datas.tfoot">
        <td v-for="col in row">{{col}}</td>
      </tr>
      </tfoot>
    </slot>
  </table>
</template>

<script>
  export default {
    name: 'MTable',
    props: {
      datas: {
        type: Object,
        default: () => ({})
      }
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



