<template>
  <li class="tree-node" :class="{'empty':node.link,'active':nowidx.indexOf(nodeidx) === 0 || cacheidx.indexOf(nodeidx) === 0}" :idx="nodeidx">
    <div class="tree-node_title" @click="nodeSelect($event, node.children)">
      <i class="tree-node_icon" v-if="node.icon" :class="node.icon"></i>
      <span class="tree-node_label">{{node.label}}</span>
      <span class="tree-angle" v-if="node.children"><span class="caret"></span></span>
      <router-link class="tree-node_link" :to="node.link" v-if="node.link && hasRouter"></router-link>
      <a class="tree-node_link" :href="formatLink(node.link)" v-if="node.link && !hasRouter"></a>
    </div>
    <ul class="tree-node_child" v-if="node.children">
      <m-tree-node v-for="(item,key) in node.children" :key="key"
                 :node="item" :nodeidx="nodeidx + '-' + key"
                 :cacheidx="cacheidx"
                 :nowidx="nowidx" @nodeSelect="childSelect"></m-tree-node>
    </ul>
  </li>
</template>

<script>
  import V from 'vue'
  import T from '../../../utils/Tool'

  export default {
    name: 'MTreeNode',
    props: {
      node: {
        type: Object
      },
      collapse: {
        type: Boolean,
        default: false
      },
      nodeidx: {
        type: String
      },
      nowidx: {
        type: String
      },
      cacheidx: {
        type: String
      }
    },
    methods: {
      nodeSelect (e, n) {
        let tempidx = e.currentTarget.parentElement.getAttribute('idx')
        let hassub = n && n.length > 0
        this.$emit('nodeSelect', tempidx, hassub)
      },
      childSelect (idx, sub) {
        this.$emit('nodeSelect', idx, sub)
      },
      formatLink (p) {
        const cname = T.jsClassName(p)
        if (cname === 'string') return p
        else throw new Error('link')
      }
    },
    computed: {
      hasRouter () {
        return V.component('router-link') !== undefined
      }
    }
  }
</script>



