<template>
  <div class="dropdown" v-click-outside="closeHandle" v-resize="handleResize" :class="[stateClass]">
    <slot name="label"></slot>
    <m-list-dropdown :class="listClass" ref="popper" v-model="popperShow" @itemSelect="itemSelect">
      <slot></slot>
    </m-list-dropdown>
  </div>
</template>

<script>
  import MPP from '../../../mixins/MPopperParent.js'

  export default {
    name: 'MDropdown',
    mixins: [MPP]
  }
</script>

<style lang="scss" src="./style.scss"></style>



