<template>
  <div class="textarea-group" :disabled="disabled">
    <textarea class="textarea" :value="value"
              :placeholder="placeholder"
              v-model="model"
              @focus="focusHandle"
              @blur="blurHandle"
    ></textarea>
  </div>
</template>

<script>
  import Forms from '../../../mixins/Forms.js'
  export default {
    name: 'MTextarea',
    mixins: [Forms],
    methods: {
      over () {
        if (!this.disabled) this.state = 'active'
      },
      leave () {
        this.state = ''
      },
      focusHandle () {},
      blurHandle () {}
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



