<template>
  <div class="row" :style="style">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: 'MRow',
    props: {
      gutter: Number
    },
    computed: {
      style () {
        let tempstyle = {}
        if (this.gutter) {
          tempstyle.marginLeft = `-${this.gutter / 2}px`
          tempstyle.marginRight = tempstyle.marginLeft
        }
        return tempstyle
      }
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



