<template>
  <div class="formbylabel radio" :class="[maskClass]" :disabled="disabled">
    <input
      type="radio"
      :name="name"
      v-model="model"
      :id="uuid"
      :value="realValue"
      @change="change">
    <label :for="uuid"><span class="_label" v-if="showlabel"><slot>{{realLabel}}</slot></span></label>
  </div>
</template>

<script>
  import Forms from '../../../mixins/Forms.js'

  export default {
    name: 'MRadio',
    mixins: [Forms],
    props: {
      label: {
        type: String,
        required: true
      },
      showlabel: {
        type: Boolean,
        default: true
      },
      name: String
    },
    computed: {
      realLabel () {
        return this._realdata[0]
      },
      realValue () {
        return this._realdata.length > 1 ? this._realdata[1] : this._realdata[0]
      }
    },
    methods: {
      change () {
        this.$emit('change')
      }
    },
    created () {
      this._realdata = this.label.split('|')
    }
  }
</script>


<style lang="scss" src="./style.scss"></style>



