<template>
  <div class="formbylabel check" :class="[maskClass]" :disabled="disabled">
    <input
      type="checkbox"
      :name="name"
      v-model="model"
      :id="uuid"
      @change="change">
    <label :for="uuid"><span class="_label" v-if="!hideLabel"><slot>{{data.label}}</slot></span></label>
  </div>
</template>

<script>
  import Forms from '../../../mixins/Forms.js'

  export default {
    name: 'MCheckbox',
    mixins: [Forms],
    props: {
      data: {
        type: Object,
        default: () => ({
          label: '',
          value: ''
        }),
        required: true
      },
      hideLabel: Boolean,
      width: Number,
      height: Number
    },
    computed: {
      model: {
        get () {
          return this._isGroup ? this.formModel : this.value
        },
        set (val) {
          let temp = val
          if (this._isGroup) {
            temp = this.value.concat()
            if (val) {
              temp.push(this.dataValue.value)
            } else {
              temp.splice(this.value.indexOf(this.dataValue.value), 1)
            }
            this.formModel = val
          }
          this.$emit('input', temp)
        }
      },
      dataValue () {
        let temp = this.data
        if (!temp.value || temp.value === '') temp.value = temp.label
        return temp
      }
    },
    watch: {
      value (n, o) {
        this.matchvalue()
      }
    },
    methods: {
      matchvalue () {
        if (this._isGroup) {
          this.formModel = false
          for (let key in this.value) {
            if (this.value[key] === this.dataValue.value) {
              this.formModel = true
              break
            }
          }
        }
      },
      change () {
        this.$emit('change')
      }
    },
    created () {
      this._isGroup = Array.isArray(this.value)
      this.matchvalue()
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



