<template>
  <div class="icon-group" :class="alignV">
    <i class="icon" :class="[icon]"></i>
    <span class="icon-label"><slot></slot></span>
  </div>
</template>

<script>
const CLASS_MAP = {
  'middle': 'icon-va icon-va-m',
  'bottom': 'icon-va icon-va-b',
  'top': 'icon-va icon-va-t'
}
export default {
  name: 'MIcon',
  props: {
    valign: {
      type: String,
      default: 'middle'
    },
    icon: {
      type: String,
      required: true
    }
  },
  computed: {
    alignV () {
      return CLASS_MAP[this.valign]
    }
  }
}
</script>

<style lang="scss" src="./style.scss"></style>



