<template>
  <transition name="fade">
    <div class="toast" :class="[postion,theme]" v-show="visible" v-html="content"></div>
  </transition>
</template>

<script>
  import MPopper from '../../../mixins/MPopper.js'
  export default {
    mixins: [MPopper],
    data: () => ({
      content: '',
      postion: 'b',
      theme: ''
    }),
    mounted () {
      const timeout = setTimeout(() => {
        this.actionPopper(false)
        clearTimeout(timeout)
      }, this.duration)
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



